    import {getUserInfo,setUserInfo} from '../../../service.js';
	export default {
		data() {
			return {
				items: [{value: "tongyi",name: '同意'},{value: "butongyi",name: '不同意'}],
				current: 1,
				
				items2: [{value: "shi",name: '是'},{value: "fou",name: '否'}],
				current2: 1,
				items3: [{value: "shi",name: '是'},{value: "fou",name: '否'}],
				current3: 1,
				acceptid:"",
				
				tiid:"",
				pdId:"",
				doc_id:"",
				status:"",
				comment:"",
				dataInfo:{}
			};
		},
		onNavigationBarButtonTap(e) {
			uni.navigateBack();
		},
		onLoad(e){
			this.acceptid = e.acceptid;
			this.tiid = e.tiid;
			this.pdid = e.pdid;
			this.doc_id = e.doc_id;
			var that = this;
			uni.request({
				url:`http://192.168.3.125:8080/ams/system/distribute.htm?module=information&user_id=${getUserInfo().userId}&acceptid=${that.acceptid}&pdid=${e.pdid}&tiid=${e.tiid}&piid=455138&docid=30359`,
				success(res){
					var data = res.data;
					that.dataInfo = data.object[0];
				}
			})
		},
		methods:{
			radioChange(evt) {
				for (let i = 0; i < this.items.length; i++) {
					if (this.items[i].value === evt.target.value) {
						this.current = i;
						this.status = evt.target.value == "tongyi" ? 0 : 1;
						break;
					}
				}
			},
			radioChange2(evt) {
				for (let i = 0; i < this.items2.length; i++) {
					if (this.items2[i].value === evt.target.value) {
						this.current2 = i;
						break;
					}
				}
			},
			radioChange3(evt) {
				for (let i = 0; i < this.items3.length; i++) {
					if (this.items3[i].value === evt.target.value) {
						this.current3 = i;
						break;
					}
				}
			},
			// 提交审核
			submitAcce(){
				uni.showLoading({
					title:"加载中",
					mask:true,
				})
				var that = this;
				uni.request({
					url:`http://192.168.3.125:8080/ams/system/distribute.htm?module=approveDoc&userId=${getUserInfo().userId}&taskInstanceId=${that.tiid}&pdId=${that.pdid}&piid=455138&docid=30359&comment=${that.comment}`,
					success(res){
						var data = res.data;
						uni.hideLoading();
					},
					fail(err){
						console.log(err)
					}
				})
			}
		}
	}
